var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{attrs:{"id":"hastalik"}},[_c('b-card',{staticClass:"mb-2",staticStyle:{"max-width":"40rem"},attrs:{"title":"Hipnoterapi","img-src":"./assets/hipno.jpeg","img-top":"","tag":"article"}},[_c('b',[_vm._v("Hipnoterapi Nedir, Ne Değildir?")]),_c('p',[_vm._v(" Hipnoz, dikkatin yoğunlaştığı ve telkin alma yeteneğinin arttığı, uyku ile uyanıklık arası bir bilinç halidir. Hipnoterapi ise, hipnozun tedavi amaçlı kullanım şeklidir. Kişinin hipnoz esnasındaki konsantrasyon durumu kullanılarak, bilinç ve bilinç dışına yönelik çerçevelenen telkinlerle destekleyici bir terapi yönelimi oluşturulur. ")]),_c('b',[_vm._v("Hipnoterapi Hangi Şikayetler için Kullanılabilir?")]),_c('p',[_vm._v(" Hipnoz, akut ve kronik ağrıdan, migren ve hatta narkozsuz ameliyatlara ve ağrısız doğuma kadar tüm ağrılarda, bağımlılıklarda ( sigara, alkol, vs), depresyon, anksiyete, panik, fobiler ( kapalı yer fobisi, uçuş fobisi, agorafobi, sosyal fobi, v.b.) stres, obezite ve yeme bozukluklarının tedavisinde travma sonu stres bozukluğu ile depresyonun v.b. gibi bir çok psikolojik hastalıkta yararlıdır. Ayrıca ağrı ( fibromiyalji gibi), genel gevşeme, obezite, stresle bağlantılı fiziksel bozukluklar, tıbbi durumlar (hipertansiyon, spastik kolon, cilt hastalıkları, astım, ülser, vs) bir çok bedensel hastalığın tedavisinde yararlıdır. Bunun yanında eğitim, sınav stresi, sporda motivasyonun artırılması, lisan kurslarında öğrenme kapasitesini arıtma ve motivasyon amaçlı, sahne korkusu, sunumlardaki performans kaygılarında ve özgüven kazanmada faydalıdır. ")]),_c('b',[_vm._v("Doğumda Hipnozun Kullanılması Mümkün müdür?")]),_c('p',[_vm._v(" Evet. Hipnoz yardımıyla doğuma \"hypnobirthing\" denmektedir. Bunun son örneği geçen yıl İngiltere'de Düşes Kate Middleton'ın doğumudur. Kısaca bilgi vermek gerekirse hypnobirthing tekniğindedoğuma hazırlanan anne adayını korkularından kurtulmak ve doğal olarak var olan doğum yapma güdüsünü keşfetmek için hipnoz kullanılır. Anne adayına gevşeme, nefes teknikleri, imgeleme teknikleri ile doğumda bedeni nasıl rahat bırakabileceklerini öğretilir. Çünkü doğum yapma güdüsü doğal olarak var olan bir güdüdür ve bunu bozan, dolayısıyla doğumda daha çok ağrıya neden olan korkular bilinçdışındadır. Günlük hayattaki doğumla ilgili negatif konuşmaların hepsi farkında olmadan bilinçdışında depolanır. Normalde bilinç dışına ulaşmak hemen hemen imkansız iken hipnoz halinde bilinçdışına rahatlıkla ulaşılır. Hypnobirthing'de konforlu bir doğum için bilinçdışı korkular ve negatif inanışlar düzeltilir. En basitinden: \"sancı\" yerine \"kasılma\" veya \"dalga\", \"ıkınma\" yerine \"bebeğe yol verme\" gibi tabirler kullanılarak doğumla ilgili olumsuz inançlar düzeltilmeye çalışılır. ")]),_c('b',[_vm._v("Hipnoterapinin Yan Etkisi veya Zararı Var mı?")]),_c('p',[_vm._v(" Hipnoterapinin hiçbir yan etkisi bulunmazken, en olumsuz yanı, herkesin eşit derecede hipnotize olmamasıdır. Danışanın hipnoterapiye alınmadan önce yapılan değerlendirme görüşmesi önemlidir. Yine kişinin terapistle kuracağı güvenli ilişki ve telkine duyarlılığı hipnoz derinliğinde belirleyicidir. Hipnoterapi bir bilinçdışı çalışmasıdır. Bilinç halindeki bir zihin, geçmiş ve şimdinin etkileriyle hareket ederken, kişi trans halindeyken tüm dirençler ortadan kalkmıştır. O esnada verilen telkinler büyük oranda gerçekleşmeye hazırdır. Kişinin değişim için istekli olması hipnoterapi için yeterlidir. Psikotik rahatsızlıklar haricinde farklı tekniklerle her danışan grubuna uygulanabilir. Çocuk, genç, yetişkinler ve yaşlılarda hedeflenen ve gerekli olan tüm davranış değişimlerinde uygulanabilen hızlı ve etkin bir yöntemdir. ")])])],1)}
